@import "../../global.scss";

.contact{
    background-color: white;
    display: flex;

    @include mobile {
        flex-direction: column;
    }

    @include heightFourFourteen {
        flex-direction: row;
    }

    .left {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;

        h6 {
            font-size: 32px;
            text-align: center;
            padding-bottom: 50px;

            @include mobile {
                padding: 0px 20px 20px 20px;
            }

            @include heightFourFourteen {
                margin-top: -50px;
                font-size: 24px;
            }

            
            @include heightThreeSixty {
                margin-top: 10px;
                font-size: 25px;
            }


            @include heightThreeTwenty {
                font-size: 15px;
                padding: 0 0 10px 0;
            }
        }

        p {
            margin-left: -30px;
            font-size: 18px;
            line-height: 42px;
            text-align: center;
            
            @include mobile {
                padding: 0 10px;
                font-size: 12px;
                font-weight: 300;
            }    

            @include heightFourFourteen {
                font-size: 12px;
                line-height: 25px;
            }
            
            @include heightThreeTwenty {
                line-height: 19px;
            }
        }


        img {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 145px; 
            width: 155px;
            margin: 50px 0 0 130px;

            @include mobile {
                margin-left: 75px ;
                border: 1px solid black;
            }

            @include heightFourFourteen {
                margin: auto;
                height: 22%;
                width: 22%;
                margin-top: 20px;
            }

            @include heightThreeTwenty {
                height: 25%;
                width: 20%;
                margin: 25px 0 0 115px;
            }
        }
    }

    .right {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        padding-bottom: 40px;

        h2 {
            font-size: 30px;

            @include mobile {
                font-style: 15px;
                margin-bottom: -90px;
            }

            @include heightFourFourteen {
                font-size: 24px;
            }

            @include squirrelPhone {
                display: none;
            }

            @include squirrelPhoneH {
                display: none;
            }

            @include heightThreeTwenty {
                font-size: 16px;
                padding-bottom: 10px;
            }
    
        }

        iframe {
            width: 500px;
            height: 280px;
            border: 0;  
            
            @include mobile {
                display: none;
            }

            @include heightFourFourteen {
                display: none;
            }
        }    

        .split {
            display: flex;
            justify-content:space-evenly;
            align-items: center;

            @include heightThreeTwenty {
                flex-direction: column;
            }

            img {
                margin-left: 40px;
                border: 3px solid green;
                height: 75px;
                width: 75px;
                margin: 0 20px;
            }
        }

        .address {
            @include mobile {
                font-size: 14px;
                padding: 0 20px;
           }

           @include heightThreeTwenty {
               text-align: center;
            font-size: 13px;
            padding-bottom: 10px;
        }
        }
    }
}