@import "../../global.scss";

.works {
    background-color: #16315c;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow {
        height: 50px;
        position: absolute;

        &.left {
            left: 100px;
            transform: rotateY(180deg);

            @include mobile{
                left: 40px;
            }

            @include hSssWTsf{
                left: 5px;
            }
        }

        &.right {
            right: 100px;

            @include mobile {
                right: 40px;
            }

            @include hSssWTsf{
                right: 5px;
            }
        }  
    }

    .slider {
        height: 450px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out;

        @include mobile {
            height: 100vh;
            flex-direction: row;
            justify-content: center;
        }

        @include squirrelPhoneH {
            flex-direction: row;
            justify-content: flex-start;
            margin-left: 20px;
        }

        @include hFeSts {
            height: 250px;
            flex-direction: row;
        }

        @include heightThreeTwenty {
            height: 350px;
            flex-direction: row;
        }

        @include heightThreeSixty {
            height: 350px;
            flex-direction: row;
        }


        .container {
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            @include squirrelPhoneH {
                width: 18%;
                justify-content: flex-start;
            }

            .item {
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;


                @include mobile {
                    width: 90%;
                    height: 225px;
                    margin: 8px 0;
                    width: 80%;
                }

                @include hFeSts {
                    height: 250px;
                }

                @include hTsfWet {
                    height: 240px;
                }

                @include heightThreeTwenty {
                    width: 80%;
                    height: 50%;
                }

                @include heightThreeSixty {
                    height: 60%;
                    width: 70%;
                }

                @include hTeWSft {
                    height: 50%;
                }

                .left {
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    @include squirrelPhoneH {
                        display: none;
                    }

                    @include hFeWett {
                        display: none;
                    }

                    .leftContainer {
                        width: 90%;
                        height: 70%;
                        display: flex;
                        align-items: center;
                        justify-content: right;

                        @include mobile {
                            margin: 0 5px 0 5px;
                        }
                        

                        .imgContainer {
                            width: 80px;
                            height: 80px;
                            border-radius: 50%;
                            background-color: rgb(245, 179, 155);
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            
                        @include mobile {
                            height: 40px;
                            width: 40px;
                        }
                            img {
                                width: 50px;

                                
                                @include mobile {
                                    width: 30px;
                                }

                                @include heightThreeTwenty {
                                    width: 30px;
                                }
                            }
                        }
                        h2 {
                            font-size: 20px;

                            @include mobile {
                                font-size: 15px;
                            }
                        }
                        p {
                            font-size: 13px;
                        }

                        span {
                            font-size: 14px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }
                    }
                    @include heightThreeTwenty {
                        display: none;
                      }
                }

                .right {
                    flex: 20;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;

                    @include mobile {
                        display: flex;
                        justify-content: flex-start;
                        align-items: flex-start;
                        height: 190px;
                        width: 200px;
                    }

                    @include hTSWSf {
                        height: 60%;
                        width: 40%;
                    }

                    @include hFeSts {
                        height: 200px;
                    }

                    img {
                        width: 500px;
                        height: 90%;

                        @include mobile {
                            display: flex;
                            justify-content: flex-start;
                            align-items: flex-start;
                            height: 100%;
                            width: 300px;
                            padding: 0 40px;
                        }

                        @include hFeSts {
                            width: 100%;
                            height: 100%;
                        }

                        @include hTSWSf {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }

                        @include hFeWett {
                            height: 200px;
                        }

                        @include widthThreeSixty {
                            margin-left: -30px;
                            width: 100%;
                        }

                        @include widthFourEleven {
                            width: 100%;
                        }

                        @include hTeWSft {
                            margin-top: 20px;
                            height: 90%;
                        }

                        @include hTsfWet {
                            height: 90%;
                        }

                        @include heightThreeTwenty {
                            height: 99%;
                        }

                        @include heightTwoEighty {
                            height: 90%;
                        }
                    }
                }
            }
        }
    }
}