@import "../../global.scss";

.testimonials {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include mobile {
    justify-content: space-around;
}

@include widthSevenThreeSix {
  margin-bottom: 100px;
  }

@include hTsfWsss {
  margin-bottom: 120px;
  }

  h1 {
    font-size: 50px;

  @include mobile {
        font-size: 20px;
    }

  @include widthSevenThreeSix {
    margin-bottom: -150px;
    font-size: 34px;
    }

    @include widthFourFourteen {
      margin-top: -25px;
      font-size: 28px;
    }

    @include widthFourEleven {
      font-size: 28px;
    }

    @include widthThreeSevenFive {
      font-size: 20px;
      margin-top: -25px;
    }

    @include widthThreeSixty {
      margin-top: -60px;
      font-size: 20px;
    }

  @include hFeSto {
    margin-bottom: -100px;
    }
    
  @include hFeWett {
      padding-bottom: 120px;
    }
    
  @include heightThreeSevenFive {
    margin-bottom: -80px;
    }

  @include hTsfWet {
    font-size: 35px;
    margin-bottom: -1px;
    }
    
  @include hTsfWsss {
    font-size: 30px;
    margin-bottom: -60px;
    }
        
  @include hTSWSf {
    margin-bottom: -100px;
    }
  }

  .container {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile {
        flex-direction: column;
        height: 90%;
    }
    
  @include heightFiveSixSeven {
    flex-direction: row;
    height: 60%;
    width: 95%;
    justify-content: space-evenly;
  }

  @include heightFourEleven {
    margin-top: -100px;
  }

  @include heightThreeSixty {
    margin-top: -50px;
  }

  @include heightThreeTwenty {
    flex-direction: row;
    height: 60%;
    width: 90%;
    justify-content: space-evenly;
  }



    .card {
      width: 250px;
      height: 70%;
      border-radius: 10px;
      box-shadow: 0px 0px 15px -8px black;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 20px;
      transition: all 1s ease;

      @include mobile {
        margin: 10px 0;
        flex-direction: column-reverse;
        text-align: center;
     }
    
    @include tablet {
      padding: 5px;
      justify-content: space-evenly;
    }
  
      &.featured{
          width: 300px;
          height: 75%;
          margin: 0 30px;
          

          @include mobile {
            width: 250px;
            height: 80px;
            margin: 1px;
        }

        @include hTsfWsss {
          width: 120px;
          }

          @include widthSevenThreeSix {
            width: 145px;
            height: 105px;
            }

            @include widthFourEleven {
              height: 80px;
              margin: 7px;
            }

            @include widthThreeSixty {
              height: 80px;
              margin: 7px;
            }

            @include widthThreeTwenty {
              height: 65px;
              margin: 5px;
            }

        @include heightFourEleven {
          height: 80%;
        }

        @include hTsfWsss {
          width: 105px;
        }

        @include heightThreeSixty {
          height: 80%;
          width: 110px;
        }

        @include heightThreeTwenty {
          height: 90%;
        }
      }

      &:hover {
          transform: scale(1.1)
      }

      .top {
        display: flex;
        align-items: center;
        justify-content: center;


        img {
          &.user {
            height: 160px;
            width: 260px;
            border-radius: 5%;
            object-fit: cover;
            margin: 0 30px;

            @include mobile {
              display: none;
            }

            @include tablet {
              width: 130px;
          }
          }
          @include heightFiveSixSeven {
            display: none;
          }
        }
      }

      .center {
        padding: 10px;
        border-radius: 10px;
        background-color: rgb(250, 244, 245);

        @include mobile {
            font-size: 12px;
            padding: 2px;
        }

        @include heightFiveSixSeven {
          font-size: 14px;
        }
      }

      .bottom {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        h3 {
          margin-bottom: 2px;
          font-size: 12px;

          @include mobile {
            font-size: 14;
            }
        }

        h4{
            color: rgb(121, 115, 115);

            @include mobile {
                font-size: 13px;
            }
        }
      }
    }
  }
}