@import "../../global.scss";

.intro {
    background-color: white;
    display: flex;

    .left {
        flex: 0.5;
        overflow: hidden;

        @include widthThreeSixty {
            margin-top: -180px;
        }

        @include widthThreeTwenty {
            margin-top: -80px;
        }

        .imgContainer {
            width: 700px;
            height: 700px;
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;

            @include heightFiveSixSeven {
                position: relative;
            }

            @include widthFourEleven {
                margin-top: -50px;
            }

            @include widthThreeSixty {
                padding-top: 220px;
            }

            @include widthThreeTwenty {
                padding-top: 70px;
            }
        }

        img {
            height: 60%;
            width: 60%;
            border: 40px solid white;
            border-radius: 15px;

            @include hOhtfwSse {
                margin-left: 300px;
            };
           
            @include hOtsswOot {
                left: 290px;
                width: 40%;
                height: 40%;
                border: 5px solid white;
            };

            @include hSseWOotf {
                left: 10px;
                width: 30%;
                height: 40%;
            };

            @include hSseWOotf {
                left: 10px;
                width: 40%;
                height: 40%;
            };

            @include hShWOotf {
                position: absolute;
                top: 110px;
                right: 600px;
                width: 30%;
                height: 58%;
            };

            @include hSsewOotf {
                left: 50px;
                width: 10%;
                height: 10%;
            };

            @include hFsewTt {
                position: absolute;
                left: 770px;
                width: 30%;
                height: 30%;
            };

            @include hFfwSts {
                position: absolute;
                top: 90px;
                left: 360px;
                height: 30%;
                width: 30%;
                border: 4px solid white;
            }

            @include hFeWett {
                position: absolute;
                top: 100px;
                left: 320px;
                height: 30%;
                width: 30%;
                border: 4px solid white;
            }           

            @include hFeSto {
                position: absolute;
                top: 110px;
                right: 140px;
                height: 30%;
                width: 30%;
                border: 4px solid white;
            }

            @include hFeSts {
                position: absolute;
                top: 90px;
                left: 350px;
                height: 30%;
                width: 30%;
                border: 4px solid white;
            }

            @include hTsfWet {
                position: absolute;
                top: 70px;
                right: 160px;
                height: 30%;
                width: 30%;
                border: 4px solid white;
            }
            
            @include hTsfWsss {
                position: absolute;
                top: 90px;
                left: 400px;
                height: 25%;
                width: 25%;
                border: 4px solid white;
            }

            @include heightThreeSixty {
                position: absolute;
                top: 90px;
                right: 120px;
                height: 25%;
                width: 25%;
                border: 4px solid white;
            }

            @include hTtWFse {
                position: absolute;
                left: 430px;
                height: 20%;
                width: 20%;
                border: 4px solid white;
            }

            @include hTeWSft {
                left: 390px;
                top: 50px;
                width: 20%;
                height: 20%;
            };
        }
    }

    .right {
        flex: 0.5;
        position: relative;

        @include widthFourFourteen {
            margin-right: 130px;
        }

        @include widthFourEleven {
            width: 60%;
        }

        @include widthThreeSixty {
            margin-right: 100px;
            width: 60%;
        }

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include widthFiveForty {
                padding-right: 135px;
            }

            @include widthFourEleven {
                margin-top: -100px;
                padding-right: 60px;
            }


            @include widthThreeSixty {
                margin-top: -80px;
                margin-right: 100px;
            }

            @include widthThreeTwenty {
                margin-right: 70px;
            }

            h1 {
                font-size: 60px;
                margin: 10px 0;

                @include heightFiveSixSeven {
                    font-size: 35px;
                }

                @include widthFiveForty {
                    font-size: 35px;
                }

                @include widthFourFourteen {
                    font-size: 28px;
                }

                @include widthThreeSixty {
                    font-size: 37px;
                }

                @include widthThreeTwenty {
                    font-size: 30px;
                }

                @include widthFourEleven {
                    font-size: 35px;
                    padding-right: 50px;
                }
            }
            h2 {
                font-size: 35px;

                @include heightFiveSixSeven {
                    font-size: 25px;
                }

                
            }
            h3 {
                font-size: 20px;
            }

            span {
                font-size: inherit;
                color: crimson;
            }

            .ityped-cursor {
                animation: blink 1s infinite;
            }

            @keyframes blink {
                50% {
                    opacity: 1;
                }
                100% {
                    opacity: 0;
                }
            }
        }

        a {
            position: absolute;
            bottom: 10px;
            left: 40%;

            img {
                width: 30px;
                animation: arrowBlink 5s infinite;
            }

            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }

        }
        p {
            width: 75%;
            margin: 20px 20px 0 0;
            line-height: 1.6;
            font-size: 20px;

            @include widthFiveForty {
                text-align: justify;
                padding-right: 150px;
            }

            @include heightFiveSixSeven {
                display: none;
            }

            @include widthFourFourteen {
                width: 70%;
                font-size: 19px;
                text-align: justify;
            }    

            @include widthFourEleven {
                font-size: 18px;
                text-align: justify;
                width: 100%;
            }

            @include widthThreeSevenFive {
                width: 100%;
            }

            @include widthThreeSixty {
                font-size: 17px;
                text-align: justify; 
            }

            @include widthThreeTwenty {
                font-size: 14px;
                text-align: justify;
            }
        }
    }
}